<template>
  <material-card
    color="accent"
    full-header
  >
    <template #heading>
      <div
        class="pa-8 white--text text-h4"
      >
        Delivery Fee
      </div>
    </template>
    <v-data-table
      :headers="headers"
      :items="data"
      :items-per-page="5"
      class="elevation-1"
    />
  </material-card>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        data: [],
        headers: [
          {
            text: 'From (Kg)',
            value: 'from_weight',
          },
          {
            text: 'To (Kg)',
            value: 'to_weight',
          },
          {
            text: 'Harga',
            value: 'price',
          },
        ],
      }
    },

    mounted () {
      this.getData()
    },

    methods: {
      getData () {
        axios.get('deliv-fee-list').then((res) => {
          this.data = res.data
        })
      },
    },
  }
</script>
